@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  }
*{
  font-family: 'Shippori Antique', sans-serif;
}
h1,h2,h3,h4,h5,h6{
  font-weight: 900;
}
span, p{
  font-weight: 400;
}
.App {
  text-align: center;
}

.background{
  position: absolute;
  top: 0px;
  right: 0px;
  height: 100vh;
  width: 100%;
  background-repeat: no-repeat;
  z-index: -1;
  filter: blur(4px);
}
.background img{

  height: 100vh;
  width: 100%;
}
.navbutton{
  color:grey;
  text-decoration: none;
  transition-property: all;
  transition-timing-function: ease-in ;
  transition-duration: 3ms;
}
.navtitle{
  color:white;
  text-decoration: none;
  transition-property: all;
  transition-timing-function: ease-in ;
  transition-duration: 3ms;
}
.navtitle:hover{
  color:white;
  font-size: 22px;
}
.navbutton:hover{
  color:white;
  font-size: 18px;
}
.responsiveCarousel {
  width: 930px;
}
.responsivePlan {
  width: 500px;
}
.responsiveImage {
  width: 930px;
}
.responsiveButton{
  width: 230px;
}
.responsiveDivButton{
  width: 260px;
}
@media (max-width: 1400px) {

  .responsiveCarousel {
    width: 640px;
    margin: 20px 0;
  }
  .responsiveButton{
    width: 180px;
  }
  .responsiveDivButton{
    width: 220px;
  }
  
}

@media (max-width: 990px) {
  .onlyMonitor {
    display: none;
  }
  .responsiveCarousel{
    width: 640px;
  }
  .responsivePrincipalCol{
    width: 0;
  }
}

@media (max-width: 766px) {

  .responsiveCarousel{
    width: 300px;
  }
  .responsivePlan {
    width: 280px;
  }
}

.App-link {
  color: #61dafb;
}

